<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <!-- <router-view/> -->
    <!-- 导航栏 -->
        <div class=" head">
            <div class="container">
                <div class="row">
                    <div class="col-md-6 head-left">
                        <img src="./assets/img/logo.png" alt="">
                    </div>
                    <div class="col-md-6 container">
                        <div class="row">
                            <div class="col-md-2 main-color main-ym">
                                首页
                            </div>
                            <div class="col-md-2 service-ym">
                                服务
                            </div>
                            <div class="col-md-2 about-ym">
                                关于
                            </div>
                            <div class="col-md-2 anlie-ym">
                                案例
                            </div>
                            <div class="col-md-2 zp-ym">
                                招聘
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
        <!-- 导航栏 end-->

        <!-- 轮播图 -->
        <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide">
                    <img src="./assets/img/swiper.jpg" alt="">
                </div>
                <div class="swiper-slide">
                    <img src="./assets/img/swiper2.jpg" alt="">
                </div>
                <!-- <div class="swiper-slide">
                    <img src="./assets/img/swiper.jpg" alt="">
                </div> -->
            </div>
            <!-- Add Arrows -->
            <!-- <div class="swiper-button-next"></div>
            <div class="swiper-button-prev"></div> -->
        </div>
        <!-- 轮播图 end-->

        <div class="title">
            <h3>品牌案例</h3>
            <p>
                <span>BRAND CASE</span>
            </p>
        </div>

        <projectList></projectList>

        <div class="ledaoImg">
            <img src="static/img/img.jpg" alt="">
        </div>
        <serviceList></serviceList>
        <bottomBar></bottomBar>
        <canvasBg></canvasBg>
        <floatBar></floatBar>
  </div>
</template>

<script>
import floatBar from './components/floatBar'
import projectList from './components/projectList'
import serviceList from './components/serviceList'
import bottomBar from './components/bottomBar'
import canvasBg from './components/canvasBg'
export default {
    name: 'App',
    components: {
        floatBar,
        projectList,
        serviceList,
        bottomBar,
        canvasBg
      }
}
$(function(){

    var mySwiper = new Swiper('.swiper-container',{
        autoplay : 4000,
        loop: true,
    });

    var shadowTop = function() {
        $(".con-shadow-before").mouseover(function() {
            $(this).addClass("con-shadow-after")
        });
        $(".con-shadow-before").mouseout(function() {
            $(this).removeClass("con-shadow-after")
        });

        $(".projectList-list-con").mouseover(function() {
            $(this).find(".projectList-list-con-txt").fadeIn(500);
        });
        $(".projectList-list-con").mouseout(function() {
            $(".projectList-list-con-txt").hide();
        })
    };
    shadowTop();
})
</script>

<style>
    .head-left{text-align: left;}
    .main-color{color: #62c4d1 !important;}
    #app {font-family: 'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;text-align: center;color: #2c3e50;margin-top: 0px;}
    .head{height: 80px;line-height: 80px;font-size: 18px;position: fixed;top: 0;z-index: 100;background-color: #fff;width: 100%;}
    .head img{width: 225px;height: 42px;}
    .head .row .col-md-2:hover{color: #62c4d1;}
    .floatSwp{position: relative;}
    html, body {position: relative;height: 100%;}
    body {background: #eee;font-family: Helvetica Neue, Helvetica, Arial, sans-serif;font-size: 14px;color:#000;margin: 0;padding: 0;}
    .swiper-wrapper{width: 100%;height: 56.4vw;}
    .swiper-container {width: 100%;margin-top: 80px;}
    .swiper-slide {text-align: center;font-size: 18px;background: #fff;/* Center slide text vertically */display: -webkit-box;display: -ms-flexbox;display: -webkit-flex;display: flex;-webkit-box-pack: center;-ms-flex-pack: center;-webkit-justify-content: center;justify-content: center;-webkit-box-align: center;-ms-flex-align: center;-webkit-align-items: center;align-items: center;}
    .swiper-slide img{width: 100%;height: 100%;}
    .ledaoImg{width: 100%;}
    .ledaoImg img{width: 100%;}
    .con-shadow-after{
    box-shadow:0 0 20px #d9d9d9;
    transform: translateY(-5px);
    -webkit-animation: danyuan_men_point .2s linear alternate forwards;
    -o-animation: danyuan_men_point .2s linear alternate forwards;
    animation: danyuan_men_point .2s linear alternate forwards;
    }
    @-webkit-keyframes danyuan_men_point {
        0% { transform:translateY(0px);}
        100%{ transform:translateY(-5px);}
    }
    @-o-keyframes danyuan_men_point {
        0% { transform:translateY(0px);}
        100%{ transform:translateY(-5px);}
    }
    @keyframes danyuan_men_point {
        0% { transform:translateY(0px);}
        100%{ transform:translateY(-5px);}
    }
</style>



