<template>
    <div class="projectList">
        <div class="ul container" v-on:click="projectListF">
            <ul class="row">
                <li v-for="projectList in projectLists" class="col-md-4">
                    <div class="con-shadow-before projectList-list-con">
                        <img class="" v-bind:src="projectList.img"/>
                        <p class="projectList-list-con-txt">描述</p>
                    </div>

                </li>
            </ul>
            <div class="projectMore title">
                <img src="static/img/more.png" alt="">
                <p>Load More</p>
            </div>
        </div>

    </div>
</template>

<script>
export default {
    name:'projectList',
    data:function(){
        return{
            projectLists:''
        }

    },
    created(){
           this.projectListF();
    },
    methods:{
        projectListF:function(){
            console.log("floatBars")
             this.$http.get('../static/project.json',{
             type: "sydata"
            }, {
                headers: {
                    "X-Requested-With": "XMLHttpRequest"
                },
                emulateJSON: true
            }).then(function (data) {
                self.b = data;
                // console.log(data.body.list)
                this.projectLists=data.body.list;
            })
         }
     }
}


</script>

<style >
body{background-color: #fff;}
li {list-style-type:none;}
.projectList{margin: 0 30px;}
.projectList-list-con{position: relative;}
.projectList-list-con-txt{display: none;position: absolute;bottom: 0;width: 100%;margin: 0;padding: 0;line-height: 30px;text-align: left;padding-left: 20px;background-color: rgba(0,0,0,0.2);color: #fff;}

.projectList ul{width: 100%;}
.projectList li{padding: 5px !important;}
.projectList img{width: 100%;}
.projectMore{margin-bottom: 50px;margin-top: 30px;}
.projectMore img{width: 72px;}
.projectMore p{color: #000 !important;margin-top: 30px !important;}
</style>



