<template>
    <ul class="floatBars">
        <!-- <li v-for="float in floats"></li> -->
        <a href="javascript:;">
            <p>领域</p>
        </a>
        <li v-for="floatText in floatTexts">{{floatText.firstName}}</li>
    </ul>

</template>

<script>
    export default{
        name:'floatBar',
        data:function(){
            return{
                floatTexts:''
            }
        },
        created(){
               this.floatBars();
        },
        methods:{
            floatBars:function(){
                 this.$http.get('../static/float.json',{
                 type: "sydata"
                }, {
                    headers: {
                        "X-Requested-With": "XMLHttpRequest"
                    },
                    emulateJSON: true
                }).then(function (data) {
                    self.b = data;
                    // console.log(data.body.employees)
                    this.floatTexts=data.body.employees;
                })
             }
         }
    }

    $(function(){
        $("li:first").css("border-top-right-radius","10px")
        $("li:last").css("border-bottom-right-radius","10px");
        console.log($("li:last"))
        $("li:last").css("border-bottom","none");
    })
</script>

<style media="screen">
.floatBars{position: fixed;width: 290px;top: 130px;padding: 0;margin: 0;z-index: 99;}
.floatBars a{position: relative;display: inline-block;width: 290px;height: 180px;background:url(../assets/img/float.png) no-repeat;background-size: 100% 100%;vertical-align: middle;}
.floatBars p{position: absolute;font-size: 40px;margin-top: 0;margin-bottom: 1rem;color: #fff;bottom: -24px;left: 18px;}
.floatBars li{width: 100%;height: 80px;line-height: 80px;background-color: #59f2fb;font-size: 23px;color: #000;border-bottom: 2px solid #56c8d8;}.
bttom{width: 100%;height: 1500px;}
</style>



